<template>
    <v-navigation-drawer v-model="drawer" clipped-left app>
    <v-list dense>

        <v-list-item to="/genres">
            <v-list-item-action>
                <v-icon>apps</v-icon>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title>Sender nach Genre</v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <v-list-item to="/countries">
            <v-list-item-action>
                <v-icon>map</v-icon>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title>Sender nach Ländern</v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <v-list-item to="/invalid" @click="fixRouting" v-if="$store.state.homeCountry!=null">
            <v-list-item-action>
                <v-img width="8" :src="$store.state.homeCountry.iconUrl"/>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title>Sender in {{ $store.state.homeCountry.homeCountryName }}</v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <!-- Only available if started from  ESP32 -->
        <v-list-item to="/setup" v-if="$store.state.esp32" >
            <v-list-item-action>
                <v-icon>settings</v-icon>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title>Info und Setup</v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <v-list-item to="/about">
            <v-list-item-action>
                <v-icon>info</v-icon>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title>Über</v-list-item-title>
            </v-list-item-content>
        </v-list-item>

        <v-list-item>
            <v-list-item-action>
                <v-icon>mdi-volume-high</v-icon>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title><br><v-slider :min="0" :max="100" :step="5" @change="changeVolume()" :error-messages="errors.volume" v-model="ws.volume"/></v-list-item-title>
            </v-list-item-content>
        </v-list-item>
        <v-list-item class="btm" @click.native="playMpdStream()">
            <v-list-item-action>
                <v-icon>mdi-information-slab-box-outline</v-icon>
            </v-list-item-action>
            <v-list-item-content>
                <v-list-item-title style="display: flex; justify-content: flex-end" v-text="ws.sw_version"></v-list-item-title>
            </v-list-item-content>
        </v-list-item>
    </v-list>

    </v-navigation-drawer>
</template>

<script>
import WebService from '@/services/WebService'
import store from '@/store/index';

  export default {
    
    data: () => ({      
            ws: {       
                heap: null,
                uptime: 'keine Daten',
                ssid: 'nicht definiert',
                volume: 20,
                streaming: false,
                bluetooth: false,
                bluetooth_name: 'nicht definiert',
                sw_version: 'nicht definiert',
            },
            changed: {
                volume: false,
                station: false
            },
            volumeFromESP: {
                volume: 20,
            },
            swVersionFromESP: {
                sw_version: ''
            },
            stationFromESP: {
                id: '',
                name: '',
                url: '',
                cc: '',
                playing: false
            },
            errors: {
                streaming: null,
                bluetooth: null,
                volume: null,
            },
            timer: '',
            waitforEsptimer: '',
            waitforEspCounter: 10 
        }),
    
    methods: {
        async getVolumefromESP(){
                new WebService().getVolumeFromESP().then(result => {
                    console.log(result);
                    this.volumeFromESP = result.data;
                    this.ws.volume = this.volumeFromESP.volume;
                }).catch(error => {
                    console.error(error);
                })
        },

        async getStationfromESP(){                
                new WebService().getStationFromESP().then(result => {
                    console.log(result);
                    this.stationFromESP = result.data;
                    var radio = this.$store.state.activeRadio
                
                    radio.active = this.stationFromESP.playing
                    radio.id = this.stationFromESP.id
                    radio.name = this.stationFromESP.name
                    radio.url = this.stationFromESP.url                    
                    radio.countrycode = this.stationFromESP.cc	
                    radio.error = false

                    this.$store.commit('setActiveRadio', radio );

                }).catch(error => {
                    console.error(error);
                })
        },

        async getSoftwareVersionfromESP() {
                new WebService().getSoftwareVersionfromESP().then(result => {
                    console.log(result);
                    this.swVersionFromESP = result.data;
                    this.ws.sw_version = 'SW Version: ' + this.swVersionFromESP.sw_version;
                }).catch(error => {
                    console.error(error);
                })
        },

        changeVolume() {
            var player = this.$store.state.musicPlayer;
            player.setVolume(this.ws.volume).then(result => {
                    this.ws.volume = result
                    this.errors.volume = ""
            }).catch(error => {
                    console.error(error);
                    this.ws.volume = !this.ws.volume;
                    this.errors.volume = "Volume Service failed"
            })
        },
        
        playMpdStream()
        {
            var player = this.$store.state.musicPlayer;
            player.playStationByNumber(0);
        },

        checkGuiIsNotSynced() {
            new WebService().checkGuiIsNotSynced().then(result => {
                    console.log(result);
                    this.changed = result.data
                }).catch(error => {
                    console.error(error);
                }).then(() => {
                    if (this.changed.volume)
                    {
                        this.getVolumefromESP()
                    }
                    if (this.changed.station)
                    {
                        this.getStationfromESP()
                    }
                }).catch(error => {
                    console.error(error);
                });
        },

        waitforEsp() {
            if (!(this.$store.state.esp32 === null) && this.$store.state.esp32 === true)
            {
                console.log("ESP is ready.");
                
                // request volume and station on startup
                this.getVolumefromESP()
                this.getStationfromESP()
                this.getSoftwareVersionfromESP()

                clearInterval(this.waitforEsptimer); // stop timer to check for ESP
                
                // ESP is available. Start timer to check for changes;
                this.timer = setInterval(this.checkGuiIsNotSynced, 1500)                
            }

            this.waitforEspCounter--;
            if (this.waitforEspCounter < 1)
            {
                console.log("NO ESP found. Giving up.");
                clearInterval(this.waitforEsptimer)
            }
        },

        fixRouting()
        {
            // I have no idea why this is needed. But without routing to home country radios does not work.
            // console.log("Current route: " + this.$route.path);
            // console.log("pushing.");
            this.$router.push({ path: this.$store.state.homeCountry.homeCountryPath })
        },
    },

    created () {
            this.waitforEsptimer = setInterval(this.waitforEsp, 2000);
    },

    beforeDestroy () {
                clearInterval(this.timer);
                clearInterval(this.waitforEsptimer)
    },

    computed: {
        drawer: {
            // getter
            get: function () {
                return this.$store.state.drawer
            },
            // setter
            set: function (newValue) {
                this.$store.commit('setDrawer', newValue );
            }
        }
    }

  }
</script>

<style>
.btm {
    position: fixed !important;
    bottom: 0 !important;
    width: 100%
}

@media (max-width: 480px) {
.btm {
        bottom: 4em !important;
  }
}
</style>