<template>
  <v-app id="inspire">
    <navigation-menu/>
    <v-app-bar
      app
      color="darkblue"
      dark
    >
      <v-app-bar-nav-icon @click.stop="toggleDrawer()" />
      <v-toolbar-title>{{ $store.state.title }}</v-toolbar-title>

      <v-progress-linear 
        :active='$store.state.loading'
        absolute
        bottom
        indeterminate
        color="amber"
      ></v-progress-linear>

      <v-spacer/>
      <div class="radioTitle">
          {{ getRadioTitle() }}
      </div>
      <start-stop-control/>
    </v-app-bar>

    <v-content v-if="$store.state.esp32!=null">
        <router-view/>
    </v-content>
  </v-app>
</template>

<script>
  import NavigationMenu from '@/components/NavigationMenu'
  import StartStopControl from '@/components/StartStop'

  export default {
    props: {
      source: String,
    },

    methods: {
        toggleDrawer(){
            var newValue = !this.$store.state.drawer
            this.$store.commit('setDrawer', newValue );
        },

        getRadioTitle(){
            var result = this.$store.state.activeRadio.name
            if (result.length>50){
                result = result.substring(0,50)+"..."
            }
            return result
        }
    },

    created() {
      this.$store.dispatch('setupTitle')
      this.$store.dispatch('setupBlacklist')
      this.$store.dispatch('setupHomeCountry')
      document.title = "Kanzler-Radio";
     //this.$store.commit('setMusicPlayer', 0);
    },

    
    components: {
      'navigation-menu': NavigationMenu,
      'start-stop-control': StartStopControl
    }
  }

</script>

<style>
    .radioTitle {
        font-size: 11px;
        font-style: italic;
        padding-right: 14px;
    }
</style>