import axios from 'axios'

export default class WebService {


    async getGenres(publicPath) {       
        console.log("getGenres")
        return await axios.get(publicPath+"data/genres.json")
    }

    async getCountry(publicPath) {
        return await axios.get( publicPath+"data/countries.json")
    }

    async getBlacklist(publicPath) {
        return await axios.get( publicPath+"data/blacklist.json")
    }
// currently there seems to be a problem with the api servers (de1, de2 ...). See: https://gitlab.com/radiobrowser/radiobrowser-api-rust/-/issues/225
// nederlands (nl1) server is still working ...
    async getCountryCodes() {
        return await axios.get( "https://nl1.api.radio-browser.info/json/countrycodes?hidebroken=true")
    }
    
    async getRadios(field, value) {
        var url = "https://nl1.api.radio-browser.info/json/stations/search?"+field+"="+value+"&codec=MP3"
        return await axios.get(url);
    }

    async getUserCountry() {
        return await axios.get("https://api.ipdata.co/?api-key=62f04882eac5c1c2469a3dee1d5c98187ba19c0a189783d372e36122")
    }

    // We retrieve the info from the ESP32
    async getInfo() {
        console.log("getInfo")
        return await axios.get("/service/info")
    }

    async checkGuiIsNotSynced() {
        return await axios.get("/service/checkguinotinsync")
    }

    async getSettings() {
        console.log("getInfo")
        return await axios.get("/service/settings")
    }

    async updateStreamInfo() {
        console.log("updateStreamInfo")
        return await axios.get("/service/streaminfo")
    }

    async getVolumeFromESP() {
        console.log("getVolumeFromESP")
        return await axios.get("/service/getvolumefromesp")
    }

    async getSoftwareVersionfromESP() {
        console.log("getSoftwareVersionFromESP")
        return await axios.get("/service/getswversionfromesp")
    }

    async getStationFromESP() {
        console.log("getStationFromESP")
        return await axios.get("/service/getstationfromesp")
    }
    
    async postBluetooth(active) {
        var action = active ? "start" : "stop"
        return await axios.post('/service/bluetooth/'+action)
    }

    async postStreaming(active, url, name, countrycode, stationid) {
        if (active) {
            const formData = new FormData()
            formData.set('url', url);
            formData.set('name', name);
            formData.set('cc', countrycode);
            formData.set('id', stationid);
            return await axios.post('/service/streaming/start', formData, {
                headers: {'Content-Type': 'multipart/form-data' }
            })
        } else {
            return await axios.post('/service/streaming/stop')            
        }
    }

    async postAnnouncementSetting(active) {
        if (active) {
            return await axios.post('/service/settings/enableAnnouncment')
        } else 
        {
            return await axios.post('/service/settings/disableAnnouncment')
        }
    }

    async postMpdStreamUrl(mpdStreamUrl) {
        const formData = new FormData()
        formData.set('mpdstreamurl', mpdStreamUrl);
        return await axios.post('/service/settings/mpdstreamurl', formData, {
			headers: {'Content-Type': 'multipart/form-data' }
		})
    }


	async postVolume(volume) {
		const formData = new FormData()
		formData.set('volume', volume);
		return await axios.post('/service/volume', formData, {
			headers: {'Content-Type': 'multipart/form-data' }
		})
	}

    async playStationByNumber(number) {
        const formData = new FormData()
		formData.set('number', number);
		return await axios.post('/service/playstationnumber', formData, {
			headers: {'Content-Type': 'multipart/form-data' }
		})
    }

    async getRadioInfo() {
        return await axios.get("/service/status");
    }

}
