/**
 * Music player API implementation which controls the ESP32 with the help of Webservice calls
 */
import WebService from "@/services/WebService"

export default class MusicPlayerESP32 {
    constructor(){
        MusicPlayerESP32.isPlaying = false;
        this.service = new WebService()
    }

    getName() {
        return "Streaming on ESP32"
    }

    async play(url, name, countrycode, stationid){
        MusicPlayerESP32.isPlaying = true;
        var response = await this.service.postStreaming(MusicPlayerESP32.isPlaying, url, name, countrycode, stationid)
        return response.status == 200 ? {url : url, playing:true} :{url : url, playing:false} 
    }

    async stop() {
        MusicPlayerESP32.isPlaying = false;
        return await this.service.postStreaming(MusicPlayerESP32.isPlaying, '', '', '', '')
    }

    async getInfo() {
        return await this.service.getRadioInfo();
    }

	async setVolume(volume){        
		await this.service.postVolume(volume).then(result => {
			console.log(result);
		})
		return volume;
	}

    async playStationByNumber(number) {
        await this.service.playStationByNumber(number).then(result => {
			console.log(result);
		})
		return number;
    }

    isPlaying(){
        return MusicPlayerESP32.isPlaying;
    }


}
