import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Genres',
    component:  () => import('../views/Genres'),
  },
  {
    path: '/genres',
    name: 'Genres',
    component:  () => import('../views/Genres'),
  },
  {
    path: '/genres/:id',
    name: 'Genre',
    component:  () => import('../views/Radios'),
    props: true, 
  },
  {
    path: '/countries',
    name: 'Countries',
    component:  () => import('../views/Countries'),
  },
  {
    path: '/countries/:id',
    name: 'Country',
    component:  () => import('../views/Radios'),
    props: true, 
  },
  {
    path: '/home/:id',
    name: 'Radios',
    component:  () => import('../views/Radios'),
    props: true, 
  },
  {
    path: '/about',
    name: 'About',
    component:  () => import('../views/About'),
  },
  {
    path: '/setup',
    name: 'Setup',
    component: () => import('../views/Setup'),
  },

]

const router = new VueRouter({
  routes
})

export default router
